import React, { Component, Fragment } from 'react';
import { Modal } from 'antd';
import { Btn } from 'components/common/button';
import FormSearchCopy from './FormSearchCopy';

export default class CopySchedule extends Component {
  state = { visible: false };

  showModal = () => {
    this.setState({
      visible: true,
    });
  };

  handleOk = e => {
    console.log(e);
    this.setState({
      visible: false,
    });
  };

  handleCancel = e => {
    console.log(e);
    this.setState({
      visible: false,
    });
  };
  render() {
    return (
      <Fragment>
        <Btn onClick={this.showModal}>Sao chép TKB</Btn>
        <Modal
          width="80%"
          style={{ top: 20 }}
          title="Sao chép TKB"
          visible={this.state.visible}
          onOk={this.handleOk}
          onCancel={this.handleCancel}
        >
          <FormSearchCopy />
        </Modal>
      </Fragment>
    );
  }
}



