import React, { Component } from 'react';
import { Row, Col, Form, Select } from 'antd';
import FilterPathEducate from 'components/common/input/FilterPathEducate';

@Form.create({ name: 'FormSearchCopy' })
class FormSearch extends Component {
  state = {};
  handleReset = () => {
    this.props.form.resetFields();
  };
  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      console.log('Received values of form: ', values);
    });
  };
  render() {
    const { getFieldDecorator } = this.props.form;
    const { Option } = Select;
    return (
      <Form onSubmit={this.handleSearch}>
        <Row gutter={24}>
          <Col span={8}>
            <Form.Item label="Từ tuần">
              {getFieldDecorator('tutuan', {})(
                <Select>
                  {[
                    'Tuần thứ 2 (13/08/2019 => 19/08/2019)',
                    'Tuần thứ 3 (21/08/2019 => 27/08/2019)',
                    'Tuần thứ 4 (01/09/2019 => 07/09/2019)',
                  ].map(item => (
                    <Option key={item} value={item}>
                      {item}
                    </Option>
                  ))}
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="Tới tuần">
              {getFieldDecorator('toituan', {})(
                <Select>
                  {[
                    'Tuần thứ 2 (13/08/2019 => 19/08/2019)',
                    'Tuần thứ 3 (21/08/2019 => 27/08/2019)',
                    'Tuần thứ 4 (01/09/2019 => 07/09/2019)',
                  ].map(item => (
                    <Option key={item} value={item}>
                      {item}
                    </Option>
                  ))}
                </Select>
              )}
            </Form.Item>
          </Col>
        </Row>
        <Row gutter={24}>
          <FilterPathEducate form={this.props.form} classes={{ multiple: true }} />
        </Row>
      </Form>
    );
  }
}

export default FormSearch;



