import React, { Component } from 'react';
import { Row, Col, Form, Select, InputNumber } from 'antd';
import FilterPathEducate from 'components/common/input/FilterPathEducate';
import { schoolYearsTime } from 'dump/data';
import { Btn, BtnSearch } from 'components/common/button';
import CopySchedule from './CopySchedule';

@Form.create({ name: 'FormSearch' })
class FormSearch extends Component {
  state = {};
  handleReset = () => {
    this.props.form.resetFields();
  };
  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      console.log('Received values of form: ', values);
    });
  };
  render() {
    const { getFieldDecorator } = this.props.form;
    const { Option } = Select;
    return (
      <Form layout="vertical" onSubmit={this.handleSearch}>
        <Row gutter={24}>
          <FilterPathEducate form={this.props.form} />
          <Col span={6}>
            <Form.Item label="Học kỳ:">
              <InputNumber min={1} max={3} defaultValue={1} style={{ width: '100%' }} />
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="Năm học:">
              {getFieldDecorator('schoolYearsTime', {})(
                <Select>
                  {schoolYearsTime.map(item => (
                    <Option key={item.code} value={item.code}>
                      {item.name}
                    </Option>
                  ))}
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="Từ tuần">
              {getFieldDecorator('tutuan', {})(
                <Select>
                  {[
                    'Tuần thứ 2 (13/08/2019 => 19/08/2019)',
                    'Tuần thứ 3 (21/08/2019 => 27/08/2019)',
                    'Tuần thứ 4 (01/09/2019 => 07/09/2019)',
                  ].map(item => (
                    <Option key={item} value={item}>
                      {item}
                    </Option>
                  ))}
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="Tới tuần">
              {getFieldDecorator('toituan', {})(
                <Select>
                  {[
                    'Tuần thứ 2 (13/08/2019 => 19/08/2019)',
                    'Tuần thứ 3 (21/08/2019 => 27/08/2019)',
                    'Tuần thứ 4 (01/09/2019 => 07/09/2019)',
                  ].map(item => (
                    <Option key={item} value={item}>
                      {item}
                    </Option>
                  ))}
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label=" ">
              <BtnSearch />
            </Form.Item>
          </Col>
          <Col span={24}>
            <Form.Item label=" ">
              <div className="btn-group">
                <Btn>Xếp lớp tự động</Btn>
                <CopySchedule />
              </div>
            </Form.Item>
          </Col>
        </Row>
      </Form>
    );
  }
}

export default FormSearch;



